.filmMob-wrapper {
  background-color: white;
  grid-area: main;
  width: 100%;
  height: 100%;
  padding-top: 5%;
  .filmHeader {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .bigMobPoster {
      width: 60%;
    }
    .filmName {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .filmQuote {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        font-size: 0.6em;
      }
    }
    .filmScore {
      font-size: 1.2em;
      margin-top: 2%;
      display: flex;
      flex-direction: column;
      width: 100%;
      align-items: center;
      justify-content: space-around;
    }
