  }
  .film {
    margin: 4% 0 4% 0;
    width: 100%;
    #yohoho {
      max-width: 100%;
      max-height: 100%;
    }
  }
  .filmDescription {
    text-align: center;
    font-size: 1em;
    padding: 2% 2% 4% 2%;
    background-color: lightgrey;
  }
  .filmFacts {
    font-size: 1em;
    border-radius: 2%;
    padding: 5%;
    background-color: lightgreen;
  }
  .similar-wrapper {
    .similar-header {
      display: flex;
      justify-content: center;
    }
    .anotherFilms {
      display: flex;
      justify-content: space-around;
      align-items: baseline;
      flex-wrap: wrap;
      margin-bottom: 1%;
      .card {
        width: 120px;
        height: 250px;
        img {
          animation: none;
        }
      }
    }
  }
}

.factsInfo {
  background-color: rgb(255, 243, 204);
  width: 30%;
  text-align: center;
  padding: 3%;
  border: 1px solid black;
  border-radius: 5%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5% auto;
}

.flags {
  display: inline-flex;
  justify-content: center;
  text-align: center;
  .flag {
    border: 1px solid black;
    width: 70px;
  }
}
